class RequestController {

  userdetails = {};
  searchFilter = {};
  lastPageStatus = 0;
  currentPage = 0;
  totalNoOfPages = 0;
  pageSize = 15;
  filterData = {};
  deleteData = 0;
  userJson = []
  nextPage() {
    this.currentPage++;
  }

  noOfPages() {
    if (this.userJson != "" || this.userJson != undefined) {
      return Math.ceil(this.userJson.length / this.pageSize);
    }
  }

  selectedJson = [];
  tempList = [];

  constructor($state, requestService, crudService, Notification) {
    "ngInject"
    this.Notification = Notification
    this.requestService = requestService
    this.mod = 'ucheck'
    this.crudService = crudService
    this.$state = $state;
    this.updateUserTable()
  }

  updateUserTable() {
    this.loadingPage = true

    try {
      this
        .requestService
        .getMakerList()
        .then((response) => {
          this.userJson = response.data
          this.loadingPage = false
        })
    } catch (err) {
      this.loadingPage = false
    }
  }

  viewUser(user) {
    this.$state.go('user.edit', { 'edit': true, 'user': user })
  }

  approve(rid) {

    this
    .requestService
    .approve(rid)
      .then((response) => {
        if (JSON.stringify(response.data.response) != "false") {
          this.Notification.success({
            message: 'Request successfully approved',
            delay: 3000,
            positionY: 'top',
            positionX: 'right'
          });
          this.updateUserTable()
        } else {
          this.Notification.error({
           // message: 'Failed accepting request',
           message: response.data.error,
            delay: 3000,
            positionY: 'top',
            positionX: 'right'
          });
        }
      })
  }

  reject(rid) {

    this
    .requestService
    .reject(rid)
      .then((response) => {
        if (response.data) {
          this.Notification.success({
            message: 'Request successfully rejected',
            delay: 3000,
            positionY: 'top',
            positionX: 'right'
          });
          this.updateUserTable()
        } else {
          this.Notification.error({
            message: 'Failed rejecting request',
            delay: 3000,
            positionY: 'top',
            positionX: 'right'
          });
        }
      })
  }
}

export default RequestController;


