import * as properties from "./config.properties"
export default class AppService{
    userBaseURL = properties.application_properties.umg_base_url;
    baseURL = properties.application_properties.base_url;
    logged_user = {};
    constructor($http){
        "ngInject"
        this.$http = $http;
    }

    initUserDetails(){
        return this.$http({
            method: 'GET',
            url: this.baseURL+"user/details",
            headers: {
                'content-type': 'application/json'
            }
        });
    }
	
	
	logout(){
        return this.$http({
            method: 'GET',
            url: this.baseURL+"users/logout",
            headers: {
                'content-type': 'application/json'
            }
        });
    }
}


