import * as properties from '../../config.properties'

export default class UserService{
    
    constructor($http){
        "ngInject"
        this.$http = $http
        this.baseURL = properties.application_properties.base_url;
    }


    getUserList(){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/user/all"
            })
    }

}


