import * as properties from '../../../../config.properties'

export default class UserTabService{

    constructor($http){
        "ngInject"
        
        this.$http = $http
        this.baseURL = properties.application_properties.base_url;
    }

    formUserDetailsGet(){
        return this.$http({
            method: "GET",
            url: this.baseURL+"form/user/details/get"
        })
    }

    formUserDetailsUpdate(req){
        return this.$http({
            method: "POST",
            url: this.baseURL+"form/user/details/update",
            data:req
        })
    }
}


