class CreateController {
isEditing=false;
Identity;
optionsList = [];
  selectedList = [];

  selectedList = []
  accessList = []
  constructor($stateParams, roleService, crudService,
    
    /*accessService,*/
     Notification, $state) {
    "ngInject"
    this.Notification = Notification
    this.$state = $state
    this.mod = 'role'
    this.crudService = crudService
    this.roleService = roleService
    this.name = 'create';
    this.$stateParams=$stateParams;
    if($stateParams.edit){
    this.isEditing=$stateParams.edit;
    this.roleformData=$stateParams.role;
    // console.log($stateParams.role)
  }

  /*
  this.accessService = accessService
    this.accessService
    .getAccessList()
      .then((response)=>{
        this.accessList = response.data
      })*/
}


  submit(){
    // console.log(this.roleformData)
    if(this.isEditing){
      this
      .crudService
        .update(this.roleformData, this.mod)
          .then((response)=>{
            if(response.data){
              this.Notification.success({message: 'Role succesfully updated', 
              delay: 3000,
              positionY: 'bottom',
              positionX: 'right'});
              this.$state.go("role.manage")
            }else{
              this.Notification.error({message: 'Error updating role', 
              delay: 3000,
              positionY: 'bottom',
              positionX: 'right'});
            }
          });
    }else{
      this
      .crudService
        .create(this.roleformData, this.mod)
          .then((response)=>{
            if(response.data){
              this.Notification.success({message: 'Role succesfully created', 
              delay: 3000,
              positionY: 'bottom',
              positionX: 'right'});
              this.$state.go("role.manage")
            }else{
              this.Notification.error({message: 'Error creating role', 
              delay: 3000,
              positionY: 'bottom',
              positionX: 'right'});
            }
          });
    }
    
  }

}

export default CreateController;



