import * as properties from '../../config.properties'

export default class RoleService {
    constructor($http){
        "ngInject"
        this.$http = $http
        this.baseURL = properties.application_properties.base_url;
    }


    getRoleList(){
        return this.$http({
            method: "GET",
            url:this.baseURL+"/role/all"
            })
    }

}


