import * as properties from './config.properties'

export default class CrudService {

    constructor($http) {
        "ngInject"
        this.$http = $http
        this.baseURL = properties.application_properties.base_url;
    }

    create(data, mod) {
        return this.$http({
            method: "POST",
            url: this.baseURL + mod + "/saveOrUpdateUser",
            data: data
        })
    }

    update(data, mod) {
        return this.$http({
            method: "POST",
            url: this.baseURL + mod + "/saveOrUpdateUser",
            data: data
        })
    }

    switchStatus(id, mod) {
        return this.$http({
            method: "POST",
            url: this.baseURL + mod + "/toggle-status",
            data: id
        })
    }

    loadCheckerRequests(mod) {
        return this.$http({
            method: "GET",
            url: this.baseURL + mod + "/checker/get"
        })
    }

    loadMakerRequests(mod) {
        return this.$http({
            method: "GET",
            url: this.baseURL + mod + "/maker/get"
        })
    }

    acceptRequest(id, mod) {
        return this.$http({
            method: "POST",
            url: this.baseURL + mod + "/accept",
            data: id
        })
    }

    rejectRequest(id, mod) {
        return this.$http({
            method: "POST",
            url: this.baseURL + mod + "/reject",
            data: id
        })
    }

    initUserDetails(){
        return this.$http({
            method: 'GET',
            url: this.baseURL+"user/details",
            headers: {
                'content-type': 'application/json'
            }
        });
    }
	
}


