import CommonStorage from "./common/common-services/storage"
import * as properties from "./config.properties"
import { not } from "@uirouter/core";

export default class AppController{
    
	user_roles = {};
	constructor(appService, $state){
		"ngInject"
		this.$state = $state
		// console.log(this.$state)
		this.appService = appService;
		this.initUserDetails()
    }
	
	logout(){
		this.appService	
			.logout()
			.then(()=>{
				document.cookie='access-token=;Path=/';
				window.location.href = '/usermanagement/lclog.html';
			});
	}
	
	initUserDetails(){
		// console.log('inside app.controller');
		this.appService.initUserDetails()
            .then((response)=>{
				// console.log('response : ', response.data);
				this.appService.logged_user = response.data;
                this.logged_user = this.appService.logged_user;
				this.allowManagement = false;
				// console.log('role length : ', this.logged_user.roles.length);
				for(var x = 0;x < this.logged_user.roles.length; x++){
					// console.log('role name : ', this.logged_user.roles[x].name);
					if (this.logged_user.roles[x].name.includes('branch_user')){
                           window.location.href = '/lcbn/'
                       }
					if(this.logged_user.roles[x].name.indexOf('umg') > -1){
						this.allowManagement = true;
						break;
					}
				}
                CommonStorage.logged_user = this.appService.logged_user;
				CommonStorage.logged_user.roles.forEach((role)=>{
					this.user_roles[role.name] = true;
				});
				CommonStorage.user_roles = this.user_roles;
            });

		
		this.umgmt_url = properties.application_properties.umgmt_url;
	}
}

AppController.$inject = ['appService'];


