class CreateController {
  isEditing = false;
  errorMessage;
  Identity;
  roleList = [];

  selectedList = []
  utilityData = [];

  advanced = false;
  heading = "Add New User";
  userFormdata = {
    UID: "jas",
    department: "",
    dob: "",
    email: "",
    employeeId: "",
    fname: "",
    lname: "",
    mobile: "",
    password: "",
  };

  constructor($stateParams, userService, userTabService, crudService, roleService, Notification, $state) {
    "ngInject"
    this.Notification = Notification
    this.userService = userService
    this.roleService = roleService
    this.$state = $state
    this.mod = 'user'
    this.crudService = crudService
    this.$stateParams = $stateParams;
    this.userTabService = userTabService
    //this.getFormUserDetails();
    if ($stateParams.edit) {
      this.isEditing = $stateParams.edit;
      this.userFormdata = $stateParams.user;
      // console.log(this.userFormdata)
      if (this.userFormdata.fullName) {
        let name = this.userFormdata.fullName.split(' ')
        this.userFormdata.fname = name[0]
        this.userFormdata.lname = name[1]
      }
    }
    if ($stateParams.edit) {
      this.heading = "Update User";
      // this.Identity=$stateParams.id;
    }

    this.roleService
      .getRoleList()
      .then((response) => {
        this.roleList = response.data
      })

  }

  submit() {
    this.userFormdata.fullName = this.userFormdata.fname + " " + this.userFormdata.lname
    if (this.isEditing) {
      this
        .crudService
        .update(this.userFormdata, this.mod)
        .then((response) => {
          if (JSON.stringify(response.data.response) != "false") {
            this.Notification.success({
              message: 'User updation request created with id :' + response.data.response,
              delay: 8000,
              positionY: 'top',
              positionX: 'right'
            });
            this.$state.go("user.manage")
          } else {
            this.Notification.error({
              message: 'Error updating user',
              delay: 3000,
              positionY: 'top',
              positionX: 'right'
            });
          }
        });
    } else {
      this
        .crudService
        .create(this.userFormdata, this.mod)
        .then((response) => {
          if (JSON.stringify(response.data.response) != "false") {
            this.Notification.success({
              message: 'User creation request created with id :' + response.data.response,
              delay: 8000,
              positionY: 'top',
              positionX: 'right'
            });
            // console.log(this.userFormdata)
            this.$state.go("user.manage")
          } else {
            this.Notification.error({
              message: 'Error creating user request',
              delay: 3000,
              positionY: 'top',
              positionX: 'right'
            });
          }
        });
    }

  }

  /*getFormUserDetails(){
    this.userTabService.formUserDetailsGet()
    .then((response)=>{
      this.utilityData=response.data;
    //  console.log(this.utilityData);
    })
    .catch((error=>console.log(error)))
  }*/

  changeData() {
    // console.log(this.userFormdata.basicDetails);
  }

  onUsernameChange() {
    if (this.userFormdata.username != this.userFormdata.employeeId) {
      this.userFormdata.username = null;
    }
    this.userFormdata.username = this.userFormdata.employeeId;
  }
}
export default CreateController;



