import * as properties from './../../../config.properties'

export default class RequestService {

    constructor($http) {
        "ngInject"
        this.$http = $http
        this.baseURL = properties.application_properties.base_url;
    }

    getMakerList() {
        return this.$http({
            method: "GET",
            url: this.baseURL + "/ucheck/checker/get"
        })
    }

    approve(rid) {
        return this.$http({
            method: "POST",
            url: this.baseURL + "/ucheck/accept",
            data: rid
        })
    }

    reject(rid) {
        return this.$http({
            method: "POST",
            url: this.baseURL + "/ucheck/reject",
            data: rid
        })
    }
}
