class ManageController {

  userdetails = {};
  searchFilter = {};
  lastPageStatus = 0;
  currentPage = 0;
  totalNoOfPages = 0;
  pageSize = 15;
  filterData = {};

  nextPage(){
    this.currentPage ++;
  }

  noOfPages(){
    if(this.roles != "" || this.roles != undefined ){
     return Math.ceil(this.roles.length / this.pageSize);
    }
  }
  roles = []

  constructor($state, roleService, crudService, Notification) {
    "ngInject" 
    this.Notification = Notification
    this.mod = 'role'
    this.crudService = crudService
    this.$state = $state;
    this.name = 'manage';
    this.roleService = roleService
    this.updateRoleTable()
  }

  updateRoleTable(){
    this.loadingPage  = true
    try{
      this.roleService
      .getRoleList()
        .then((response)=>{
          this.roles = response.data
          this.loadingPage = false
        })
    }catch(err){
      this.loadingPage  = false
    }
    
  }

  editRole(role){
    this.$state.go('role.edit', { 'edit': true, 'role': role })
  }

  
  deleteRole(id){
    this.crudService
    .switchStatus(id, this.mod)
      .then((response)=>{
        if(response.data){
          this.Notification.success({message: 'Role status changed successfully', 
          delay: 3000,
          positionY: 'top',
          positionX: 'right'});
          this.updateRoleTable()
        }else{
          this.Notification.error({message: 'Failed changing role status', 
          delay: 3000,
          positionY: 'top',
          positionX: 'right'});
        }
      })
  }

}

export default ManageController;



