import angular from 'angular';
import uiRouter from 'angular-ui-router';
import Common from './common/common';
import Components from './components/components';
import AppComponent from './app.component';
import  ServiceInterceptor from './app.interceptor';
import CommonStorage from './common/common-services/storage';
import AppService from "./app.service"
import 'normalize.css';
import multipleSelect from 'angular-multiple-select';
import crudService from './crud.service'
import notification from 'angular-ui-notification'

import userTabService from './components/admin/utils//usertab/usertab.service'
require('../assets/lib/angular-froala.js')
require('../assets/ngtrix/angular-trix.min.js')


angular.module('app', [
    uiRouter,
    Common,
    Components,
    'multipleSelect',
    'froala',
    'ui-notification',
    'angularTrix'
  ])
  .value('froalaConfig', {
    toolbarInline: false,
    placeholderText: 'Enter Text Here'
})
  .config(($locationProvider,  $httpProvider, NotificationProvider) => {
    "ngInject";
    // @see: https://github.com/angular-ui/ui-router/wiki/Frequently-Asked-Questions
    // #how-to-configure-your-server-to-work-with-html5mode
    $locationProvider.html5Mode(true).hashPrefix('!');
    $httpProvider.interceptors.push('app_intercept');

    NotificationProvider.setOptions({
      delay: 10000,
      startTop: 20,
      startRight: 10,
      verticalSpacing: 20,
      horizontalSpacing: 20,
      positionX: 'left',
      positionY: 'bottom'
  });
  })
  .directive('fileModel', ['$parse', function ($parse) {
    return {
        restrict: 'A',
        link: function(scope, element, attrs) {
            var model = $parse(attrs.fileModel);
            var modelSetter = model.assign;
    
            element.bind('change', function(){
                scope.$apply(function(){
                    modelSetter(scope, element[0].files[0]);
                });
            });
        }
    };}])
  .component('app', AppComponent)
  .service("commonStorage",CommonStorage)
  .service("appService",AppService)  
  .service('app_intercept', ServiceInterceptor)
  .service('crudService', crudService)
  .service("notification", notification)
  .service('userTabService', userTabService);
  



